.sidebar-header {
    font-family: "Titillium Web",Helvetica,Arial,sans-serif;
    font-size: 22px;
    font-weight: 600;
    margin-top: 16px !important;
    margin-bottom: 0;
    color: #0a304e;
    line-height: 1.1;
    cursor: pointer;
}

.sidebar-header:hover {
    color: #0a304e; 
}

.page-sidebar-menu {
    list-style: none;
    margin: 10px 0 0;
    padding: 0;
}

.nav-link {
    text-shadow: none;
    text-decoration: none;
    margin-left: 10px;
    color: #626262;
    font-size: 18px;
    font-weight: 400;
    position: relative;
    border: 0;
    padding: 5px 0
}

.nav-link:hover {
    text-decoration: underline;
    color: #626262;
}
.page-sidebar-menu li a:hover {
    text-decoration: underline;
}
.page-sidebar-menu .nav-link.active {
    background-color: #f5f5f5;
    border-left: 2px solid #ddd;
    padding-left: 10px;
}
.nav-link {
  font-family: "Titillium Web", sans-serif;
  font-size: 18px;
  color: #0a304e !important;
}

.nav-item.dropdown:hover, .nav-item:hover {
  background-color: white;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.nav-item.dropdown:hover .dropdown-content {
  display: block !important; 
}

.dropdown-content .dropdown-item, .dropdown-content .dropdown-divider {
  float: none;
  color: black;
  display: block;
  text-align: left;
}
.dropdown-content .dropdown-item:hover {
  background-color: whitesmoke;
  color: cornflowerblue;  
}
.dropdown-content .dropdown-item {
  cursor: pointer;
}
.navbar-toggler {
  font-family: "Titillium Web", sans-serif;
  font-size: 18px;
  color: #0a304e !important;
}

.navbar.navbar-default {
  padding-left: -15px;
  padding-right: -15px;
}
.background-nav {
    background-color: #0a304e;
}
.custom-margin {
    margin-bottom: 0;
}

.search-bar {
    position: relative;
}

.magnifying-glass {
    position: absolute;
    top: 50%;
    z-index: 999;
}
.carousel-background {
    background-color:#0A304E;
    font-family: "Titillium Web",sans-serif;
    right: 0;
    left: 0;
    bottom: 0;
    text-align: left;
    padding: 10px 20px 15px;
    z-index: 0;
    height: 75px;
}

.carousel-control-prev-icon {
    font-size: 100px;
}

.custom-size {
    height: 320px;
}

.carousel-caption p {
    margin-bottom: 0;
}
.breadcrumb {
  text-transform: uppercase;
  color: #626262;
  font-size: 12px;
  font-weight: 700;
  margin-top: 20px;
  border-bottom: 1px solid #eee;
  background: 0 0;
  margin: 0px;
  border-radius: 0;
  line-height: 1.42857;
}

.breadcrumb a {
  color: #0a304e !important;
}

.breadcrumb li::before {
  content: ">";
  padding-left: 5px;
  padding-right: 3px;
  font-size: 14px;
  color: #0a304e !important;
}
.wrapper {
  min-height: 100%;
  padding-bottom: -215px;
  background-color: #fff;
}

:root {
    --space: 1.5em 0;
    --space: 2em 0;
}

.Site {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 100vh;
}

.Site-content {
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    padding: 2em 0 2em 0 0;
    padding: var(--space) var(--space) 0;
    width: 100%;
}

.Site-content:after {
    content: '\A0';
    display: block;
    margin-top: 2em 0;
    margin-top: var(--space);
    height: 0;
    visibility: hidden;
}

.footer {
    background: #D3D3D3;
    height: 520px;
    

}

.image {
    width: 260px;
    height: auto;
}

  table {
